@import url(https://fonts.googleapis.com/css?family=Lato);
body, html{
  width: 100vw;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-image: url('https://www.gannett-cdn.com/-mm-/885527418e6b47858deedfd5ea3d4f3e60e6d0e0/c=325-0-1722-786/local/-/media/2020/03/01/USATODAY/usatsports/MotleyFool-TMOT-85ee6821-be2e4caf.jpg');
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

body::-webkit-scrollbar {
  display: none;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App header {
    position: absolute;
    width: 100%;
    height: 8%;
    left: 0;
    top: 0;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}

.App header{
    position: fixed;
    z-index:1;
    box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3);
    /* background-image: linear-gradient(to top, #9EFBD3 0%, #57E9F2 48%, #45D4FB 100%); */
    background-color: rgba(24, 28, 31, 0.8);}

.App .logo{
    position: absolute;
    width: 260px;
    height: 38px;
    left: 40%;
    top: 13%;
}

.mob-msg{
    display: none;
}

@media(max-width: 1024px){
    .App header{
        height: 4.5%;
    }
}

@media(max-width: 599px){
    .App .logo{
        left: 15%;
    }

    .mob-msg{
        position: absolute;
        top: 45%;
        color: teal;
        font-style: italic;
        font-family: cursive;
        left: 10%;
        display: block;
        font-size: xx-large;
    }
}
.ContentTable{
    width: 65%;
}

.ContentTable .table{
    margin: 2% 0 2% 3%;
    min-height: 30%;
    max-height: 95%;
    border: 1px solid #E4E4E7;
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12)
    ;
    background-image: linear-gradient( 108.1deg,  rgba(167, 220, 225, 0.767) 11.2%, rgba(217, 239, 242, 0.774) 88.9% );
}

.ContentTable .table thead{
    position: static;
    width: 952px;
    left: 0px;
    top: 0px;
    background: #F4F5F7;
    flex: none;
    order: 0;
    flex-grow: 0;
    margin: 0px 0px;
}

td .alert{
    border-radius: 20px;
}

.ContentTable #spinner{
    top:50%;
}

#emptyMsg{
    text-align: center;
}

#spinner{
    display: block;
    position: absolute;
    top: 40%;
    left: 45%; 
}

.pagination{
    position: absolute;
    right: 1%;
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12)
    ;
}

@media(max-width: 1024px){
    .ContentTable{
        width: 95%;
    }

    .pagination{
        right: 4.5%;
    }
}

@media (max-width: 599px) {
    .ContentTable table th:first-child, .ContentTable table td:first-child {
        position: -webkit-sticky;
        position: sticky;
        left: 0;
        background-color: #F4F5F7;
    }

    .ContentTable .table{
        font-size: 0.5em;
        margin: 10% 8% 2% 8%;
        width: 85%;
    }
}
.Backdrop {
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 100;
    left: 0;
    top: 0;
    background-color: rgba(0,0,0,0.81);
}
.Modal {
    position: fixed;
    z-index: 500;
    background-image: linear-gradient( 358.7deg,  rgba(42, 136, 157, 0.719) 1.7%, rgba(122, 197, 214, 0.733) 51.1%, rgba(192, 212, 221, 0.74) 95.5% );
    padding: 16px;
    box-sizing: border-box;
    top:30%;
    transition: all 0.3s ease-out;
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12)
    ;
    border-radius: 9px;
    max-height: calc(100vh - 110px);
    overflow-y: auto;
}

.Modal::-webkit-scrollbar {
    display: none;
}

@media (min-width: 600px) {
    .Modal {
        left: calc(50% - 320px);
    }
}
.Main{
  font-size: smaller;
}

#dateRange{
    width: 90% !important;
    left: 10%;
    top: 0%;
}

.dropdown-menu{
    background-image: linear-gradient( 108.1deg,  rgba(167,220,225,1) 11.2%, rgba(217,239,242,1) 88.9% );  
}

.btn { 
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12)
  ;
}

.btn:hover {
   background-position: right center; /* change the direction of the change here */
   color: #fff;
   text-decoration: none;
}

#btn1{
    /* background-image: linear-gradient(to top, #9efbd381 0%, #57eaf283 48%, #45d4fb67 100%); */
    margin: 6% 0 1% 34%;
    float: left;
    border: none;   
    font-size: small;
    background-image: linear-gradient( 108.1deg,  rgba(167, 220, 225, 0.767) 11.2%, rgba(217, 239, 242, 0.774) 88.9% ); 
}

#btn3{
    margin: 6% 0 1% 2%;
    float: left;
    border: none; 
    font-size: small;
}

#btn2{
    /* background-image: linear-gradient(to top, #9EFBD3 0%, #57E9F2 48%, #45D4FB 100%); */
    margin: 6% 1% 1% 0;
    float: right;
    border: none; 
    font-size: small;
    background-image: linear-gradient( 108.1deg,  rgba(167, 220, 225, 0.767) 11.2%, rgba(217, 239, 242, 0.774) 88.9% );
}

#red{
    background-color: #FDE2E1;
    color: #981B1C;
}

#green{
    background-color: #DEF7EC;
    color: #03543F;
}

#yellow{
    background-color: #FEF3C7;
    color: #92400F;
}

#main-content{
    display: flex;
    flex-direction: row;
    width: 100%;
}
.side{
    width: 30%;
    margin-left: 2%;
    margin-top: 1.2%;
}

footer{
   padding-top: 100px;
}

@media(max-width: 1024px){
    #btn1{
        margin: 10% 0 2% 8% ;
        font-size: 0.5em;
    }

    #main-content{
        flex-direction: column;
    }

    #btn2{
        margin: 10% 7% 2% 0 ;
        font-size: 0.5em;
    }

    .dropdown-item{
        font-size: 0.5em;
    }
}

@media (max-width: 599px) {
    .Main{
        display: none;
    }
}
.LaunchDetails{
    color: white;
}

.LaunchDetails #logo{
    width: 72px;
    height: 72px;
    margin-right: 20px;
}

.LaunchDetails .table{
    color: white;
}

.LaunchDetails .table tbody :first-child td{
    border: none;
}

.LaunchDetails .modal-head{
    display: flex;
}

.LaunchDetails .alert{
    width: auto;
    height: 5px;
    border-radius: 20px;
}

.LaunchDetails .Modal{
    left: 12%;
    top: 10%;
    width: 80%;
}

.close{
    position: absolute;
    right: 1.5%;
    top:0.5%;
}

@media (min-width: 600px) {
    .LaunchDetails .Modal{
        width: 500px;
        left: calc(50% - 250px);
    }
}
#launch-chart{
    height: 33%;
    border-radius: .53rem;
    margin-top: 5%;
    box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
    background-color: rgba(250, 250, 250, 0.411);
}

@media(max-width: 1024px){
   #launch-chart{
    margin-top: 5%;
    width: 60%;
   }
}
.countdown{
  box-shadow:
    0 2.8px 2.2px rgba(0, 0, 0, 0.034),
    0 6.7px 5.3px rgba(0, 0, 0, 0.048),
    0 12.5px 10px rgba(0, 0, 0, 0.06),
    0 22.3px 17.9px rgba(0, 0, 0, 0.072),
    0 41.8px 33.4px rgba(0, 0, 0, 0.086),
    0 100px 80px rgba(0, 0, 0, 0.12);
  background-color: rgba(250, 250, 250, 0.411);
  border-radius: .53rem;
  height: 63%;
}

.countdown h4{
  font-family: serif;
  text-align: center;
  padding: 1.5% 0;
}

.countdown-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
}

.countdown-item {
  font-size: 40px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  line-height: 30px;
  margin: 10px;
  padding-top: 10px;
  position: relative;
  width: 100px;
  height: 100px;
}

.countdown-item span {
  color: #333;
  font-size: 12px;
  font-weight: 600;
  text-transform: uppercase;
}

.countdown-svg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100px;
  height: 100px;
}
