.App header {
    position: absolute;
    width: 100%;
    height: 8%;
    left: 0;
    top: 0;
    box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
}

.App header{
    position: fixed;
    z-index:1;
    box-shadow: 0 3px 5px rgba(57, 63, 72, 0.3);
    /* background-image: linear-gradient(to top, #9EFBD3 0%, #57E9F2 48%, #45D4FB 100%); */
    background-color: rgba(24, 28, 31, 0.8);}

.App .logo{
    position: absolute;
    width: 260px;
    height: 38px;
    left: 40%;
    top: 13%;
}

.mob-msg{
    display: none;
}

@media(max-width: 1024px){
    .App header{
        height: 4.5%;
    }
}

@media(max-width: 599px){
    .App .logo{
        left: 15%;
    }

    .mob-msg{
        position: absolute;
        top: 45%;
        color: teal;
        font-style: italic;
        font-family: cursive;
        left: 10%;
        display: block;
        font-size: xx-large;
    }
}